.container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-image: url("../../assets/mountain1.jpg");
  background-color: rgba(0, 0, 0, 0.2);
  background-size: cover;
  background-blend-mode: darken;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapper {
  height: 52.5vh;
  width: 25vw;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
}

.wrapper > h2 {
  color: #171097;
  text-align: center;
  margin: 2.5rem 0;
  font-size: 36px;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 42px;
}

form > input {
  width: 50%;
  outline: none;
  border: none;
  border-bottom: 1px solid #333;
}

form > button {
  outline: none;
  background-color: blue;
  color: #fff;
  border-radius: 12px;
  padding: 0.5rem 1.25rem;
  font-size: 18px;
  cursor: pointer;
  border: 1px solid transparent;
  margin-top: 1rem;
}

form > button:hover {
  background-color: #fff;
  border-color: blue;
  color: blue;
}
